body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  top: 4%;
  left: 5%;
  width: fit-content;
  box-sizing: border-box;
  margin: auto;
  padding: 0 20px 20px;
  border-radius: 4px;
  opacity: 1;

  color: #141f33;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif, Helvetica Neue;
  font-weight: 300;
  line-height: 1.5;

  /* //Glass Effect */

