// const background = 'https://cdn.pixabay.com/animation/2024/05/30/10/16/10-16-19-835_512.gif'
// const background = 'https://cdn.pixabay.com/animation/2024/05/30/10/01/10-01-43-509_512.gif'
// const background = 'https://mir-s3-cdn-cf.behance.net/project_modules/max_1200/80740a27869045.5636c15d36644.jpg'
