const background = 'https://cdn.pixabay.com/photo/2016/09/29/10/08/halloween-1702531_1280.jpg'
// const background = 'https://cdn.pixabay.com/photo/2023/10/17/19/29/pumpkins-8322245_1280.jpg' */

  background: url("https://cdn.pixabay.com/photo/2016/09/29/10/08/halloween-1702531_1280.jpg");
  background-size: cover;
  border-radius: 16px;

  backdrop-filter: blur(10px);

  /* max-height: 300px; */
  overflow-y: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: 16px;
  color: #e3e3e3;
  text-shadow: #4c4b4b 1px 0 10px;
}

em {
  font-weight: 400;
  font-style: normal;
}
table {
  opacity: 1;
  background: transparent;
  border-radius: 4px;
}
th {
  background: black;
  color: white;
}
th,
td {
  border: 1px solid #000;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 16px;
}
td {
  background: black;
  opacity: 0.8;
  color: white;
}

.todo-container p {
  padding: 15px;
  border-radius: 8px;
  color: white;
  font-weight: bold;
  margin: 4px;
  /* // opacity:0.5; */
  color: maroon;
}

.filter-group {
  display: flex;
  flex-wrap: wrap;
}

.filter-buttons {
  background: black;
  border: none;
  color: white;
  padding: 10px 16px;
  margin: 12px 2px;
  border-radius: 4px;
  font-weight: bold;
}

.filter-buttons:hover,
.filter-buttons-active {
  background: white;
  color: black;
  cursor: pointer;
}

.tag {
  display: inline-block;
  margin: auto;
  margin-right: 4px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
